*{
  margin: 0;
  padding: 0;
  padding-top: 0.5%;
  box-sizing: border-box;
}
html{
  font-size: 62.5%;
}
body{
  background:black;
  color:white;
  font-family: 'Lato';
}
.App{
  min-height: 100vh;
  display:flex;
}
.sidebar{
  flex:3;
  border-right: 1px solid rgb(100,100,100);
  display: flex;
  flex-direction: column;
}

.main{
  flex: 9;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 6rem 10rem;
}
.logo{
  width: 70px; 
  height: auto; 
  margin-right: 1rem;
  
}
.brand{
  font-size: 2rem;
}
.upperSide{
  padding: 2.5rem;
  border-bottom: 1px solid rgb(100,100,100);
  flex-grow: 1;
}
.upperSideTop{
  display:flex;
  align-items: center;
  margin-bottom:2.5rem;
}
.midBtn{
  background:grey;
  border:none;
  color:black;
  padding:1.5rem;
  font-size: 1.5rem;
  font-weight: 700;
  width:100%;
  max-width:25rem;
  display:flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  margin-bottom: 3rem auto;
  border-radius: 0.5rem;
}
.lowerSide{
  padding-top: 10%;
  padding: 1rem;
  font-size:medium;
  padding: 1rem 3rem 10rem 3rem;
}
.listItems{
  margin: 0.5rem;
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  padding-bottom: 0.5rem;
  padding-right: 0.5rem;
  font-weight: 900;

}
.listItemsImg{
  margin: 1rem;
  height: 2rem;
}
.query{
  display:flex;
  align-items: center;
  background: transparent;
  color:white;
  padding:1rem;
  width:100%;
  max-width: 25rem;
  margin:1rem auto;
  border-radius: 0.5rem;
  border: 1px solid rgb(98,98,98,1);
}
.query>img {
  margin-right:2rem;
  object-fit: cover;
  height: 1.75rem;
}

.chats{
  overflow-y: scroll;
  scroll-behavior: smooth;
  width:100%;
  max-width: 70rem;
  height: calc(100vh - 17rem);
}

.chats::-webkit-scrollbar {
  display: none;  
}

.chat{
  margin:1rem;
  padding:2rem 3rem;
  font-size: 1.2rem;
  display: flex;
  align-items:flex-start;
  text-align: justify;
}

.chatImg{
    object-fit: cover;
    width: 3.5rem;
    margin-right: 2rem;
    border-radius: 0.5rem;
}

.bot{
   background: grey;
   width: fit-content;
   border-radius: 0.5rem;
}
.chatFooter{
  margin-top:auto;
  width:100%;
  display: flex;
  flex-direction:column;
  justify-content: flex-end;
  align-items: center;
  padding: 0 1 rem;
  position: relative;
}

.inp{
  padding: 1rem;
  background:rgb(28,30,58,1);
  display:flex;
  align-items: center;
  border-radius: 0.5rem;
  width: 68rem;
  position: relative;
  bottom: 1rem;
  right: 1rem;
}

.inp>input{
  width:calc(100% - 3rem);
  outline:none;
  padding: 1.25rem;
  color:white;
  height: 100%;
}

.inp>input, .send{
     background: transparent;
     border: none;
     height: 1.75rem;
     padding-left: 1.25rem ;
    margin-left: 1rem;
     
    
}

.chatFooter>p{
  padding-bottom: 1rem;
   margin: 0;
  text-align: center;
  
}
