/* Styles for Offcanvas Navbar */

.navbar .navbar-brand{
    color: black;
    font-size: 2.25rem;
    font-weight: 600;
  padding-left: 2%;
}
.navbar .container-fluid{
    background-color: white;
    max-width: 1200px; /* Adjust the max-width as needed */
  width: 100%; 
}
/* Adjust the background color of the offcanvas */
.offcanvas {
    background-color: darkgrey; /* Light background color */
    padding: 20px; /* Add padding inside the offcanvas */
  }
  
  /* Customize the header of the offcanvas */
  .offcanvas-header {
    border-bottom: 1px solid white; /* Add a border at the bottom of the header */
  }
  
  /* Style the title in the offcanvas header */
  .offcanvas-title {
    font-size: 2.25rem; /* Adjust the font size */
    font-weight: 600; /* Adjust font weight */
  }
  
  /* Style the close button in the offcanvas header */
  .btn-close {
   
    background-color: white; /* Primary theme color */
    color: #fff; /* Text (icon) color */
    border: 1px solid white; /* Border color to match the button */
    border-radius: 50%; /* Round shape */
    padding: 0.5rem; /* Padding for click area */
    font-size: 1.25rem; /* Font size of the icon */
    line-height: 1; /* Center the icon */
    transition: background-color 0.3s, color 0.3s; /* Smooth transition */
    /* Optional: Change close button color to match the theme */
  }
  
  /* Style the list items in the offcanvas navbar */
  .offcanvas-body .navbar-nav .nav-link {
    color: #000; /* Set the color of the links */
    font-size: 1.5rem; /* Adjust font size of the links */
  }
  
  /* Customize the dropdown menu in the offcanvas */
  .offcanvas-body .dropdown-menu {
    background-color: white; /* Match the offcanvas background color */
  }
  
  /* Style the dropdown items */
  .offcanvas-body .dropdown-item {
    color: #000; /* Set color for dropdown items */
    font-size: 1.5rem; /* Adjust font size */
  }
  
  /* Style the search form */
  .offcanvas-body .form-control {
    border-radius: 1.25rem;
    border-color: #000; /* Rounded corners for input */
  }
  
  .offcanvas-body .btn-outline-success {
    border-color: black; /* Border color of the button */
    color: black; /* Text color of the button */
  }
  
  .offcanvas-body .btn-outline-success:hover {
    background-color:black; /* Background color on hover */
    color: #fff; /* Text color on hover */
  }
  